import { Component, OnDestroy, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { GetWorkAreaInfo } from '../../../@store/global-info/global-info.actions';
import { GlobalInfoState } from '../../../@store/global-info/global-info.state';
import { Subscription } from 'rxjs';
import { IElement } from '../../../@store/global-info/global-info.model';
import { InformService } from '../../../@core/inform.service';
import { StorageService } from '../../../@core/storage.service';
import { NavigationExtrasService } from '../../../@core/navigation-extras.service';
import { SignupBasePage } from '../../../signup-flow/signup-base.page';
import { UtilsService } from '../../../@core/utils.service';
import { GoogleAnalyticsService } from '../../../@core/google-analytics.service';
import { Camera } from '@ionic-native/camera/ngx';

const WORK_AREA_MAX_ITEMS = 3;
const PAGE_POSITION = 2;

@Component({
  selector: 'app-work-area',
  templateUrl: './work-area.page.html',
  styleUrls: ['./work-area.page.scss'],
})
export class WorkAreaPage extends SignupBasePage implements OnInit, OnDestroy {
  public workAreaList: IElement[];
  public max = WORK_AREA_MAX_ITEMS;
  public selectedItems: string[] = [];
  private subscription$: Subscription;

  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    navigationExtrasService: NavigationExtrasService,
    googleAnalyticsService: GoogleAnalyticsService
  ) {
    super(
      location,
      router,
      store,
      utilsService,
      camera,
      informService,
      storage,
      navigationExtrasService,
      googleAnalyticsService
    );
  }

  ngOnInit() {
    this.loadWorkAreaInfo();
    // override showBackButton
    this.showBackButton = this.checkGoBack();
  }

  private loadWorkAreaInfo(): void {
    this.store.dispatch(new GetWorkAreaInfo());
    this.subscription$ = this.store.select(GlobalInfoState.getWorkArea).subscribe((data) => {
      this.workAreaList = data;
    });
  }

  public selectItem(id: string): void {
    if (this.isItemSelected(id)) {
      this.selectedItems = this.selectedItems.filter((item) => item !== id);
    } else {
      if (this.selectedItems.length < WORK_AREA_MAX_ITEMS) {
        this.selectedItems.push(this.workAreaList.find((item) => item.id === id)?.id);
      }
    }
  }

  public isItemSelected(id: string): boolean {
    return this.selectedItems?.indexOf(id) !== -1;
  }

  public reachMaxItemsSelected(): boolean {
    return this.selectedItems.length === WORK_AREA_MAX_ITEMS;
  }

  public async nextPage(): Promise<void> {
    if (this.selectedItems.length !== 0) {
      // await this.updateSignupFlowInfo(PAGE_POSITION, 'works', this.selectedItems);
      await this.updateSignupFlowInfo(PAGE_POSITION, 'works', null);
      // go next page
      this.goNextFlowPage(PAGE_POSITION);
    } else {
      this.informService.showWarning('workArea.selectionMessage');
    }
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }

  private disconnectObservers(): void {
    this.subscription$.unsubscribe();
  }
}
